import {  
    mapState,  
    mapMutations  
} from 'vuex'

export default {
	name: 'NavBar',	
	props: {
		
	},
	
	data() {
		return {
			title: 'Hello',
			currentUser: {
				name: '汪大头'
			}, 
		}
	},
	computed: {
		isIndex: function() {
			return (this.curpage === 'index')
		},
		isUserCenter: function() {
			return (this.curpage === 'userCenter')
		},
		avatarUrl: function() {
			return 'url(https://ossweb-img.qq.com/images/lol/web201310/skin/big10001.jpg)'
		},
		...mapState(['curpage'])
	},
	onLoad() {

	},
	methods: {
		...mapMutations(['changePageTo', 'setTitle']),
		naviTo: function(page) {
			this.changePageTo(page)
			if (page === 'index') {
				this.setTitle('志愿广场')
			}
			if (page === 'userCenter') {
				this.setTitle('个人中心')
			}
		}
	}
}
